import React, { useEffect, useRef, useState } from 'react';

const VoiceBox2 = () => {
  const recognitionRef = useRef(null); // مرجع برای SpeechRecognition
  const [finalTranscript, setFinalTranscript] = useState(''); // متن نهایی
  const [interimTranscript, setInterimTranscript] = useState(''); // متن موقت
  const [isRecording, setIsRecording] = useState(false); // وضعیت ضبط


  // مقداردهی به SpeechRecognition
  const initializeRecognition = () => {
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;

    if (!SpeechRecognition) {
      alert('Web Speech API در این مرورگر پشتیبانی نمی‌شود.');
      return null;
    }

    const recognition = new SpeechRecognition();
    recognition.lang = 'fa-IR'; // زبان فارسی
    recognition.interimResults = true; // نمایش نتایج موقت
    recognition.onresult = (event) => {


      console.log(event);
      const interimTranscript = event.results[0][0].transcript;

      // setFinalTranscript(interimTranscript);
      setInterimTranscript(interimTranscript);
    };

    recognition.onerror = (event) => {
      console.error('خطا در ضبط:', event.error);
    };

    return recognition;
  };
  useEffect(() => {
    if (interimTranscript !== '') {



    }
  }, [interimTranscript]);

  const startRecognition = () => {
    if (!recognitionRef.current) {
      recognitionRef.current = initializeRecognition();
    }
    if (recognitionRef.current) {
      recognitionRef.current.start();
      setIsRecording(true);
    }
  };

  // توقف ضبط
  const stopRecognition = () => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      setIsRecording(false);
      setFinalTranscript((prev) => `${prev} ${interimTranscript}`.trim());
      setInterimTranscript('')
    }
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h1>ضبط صدا: {isRecording ? 'در حال ضبط...' : 'متوقف'}</h1>
      <button
        type="button"
        style={{
          padding: '20px',
          fontSize: '18px',
          backgroundColor: isRecording ? 'red' : 'green',
          color: 'white',
          border: 'none',
          borderRadius: '10px',
        }}
        onMouseDown={startRecognition} // شروع ضبط با نگه داشتن دکمه
        onMouseUp={stopRecognition} // توقف ضبط با رها کردن دکمه
        onTouchStart={startRecognition} // برای موبایل
        onTouchEnd={stopRecognition} // برای موبایل

      >
        {isRecording ? 'ضبط کنید...' : 'برای ضبط نگه دارید'}
      </button>
      <p>sss</p>
      <p style={{ marginTop: '30px', fontSize: '18px' }}>{finalTranscript}</p>
      {isRecording && <p style={{ marginTop: '30px', fontSize: '18px' }}>{interimTranscript}</p>}
    </div>
  );
};

export default VoiceBox2;
